<script>
  import TabBar from './tab-bar';
  module.exports = {
    name: 'ElTabs',

    components: {
      TabBar
    },

    props: {
      type: String,
      activeName: String,
      closable: {
        type: Boolean,
        default: false
      },
      value: {}
    },

    data() {
      return {
        children: null,
        currentName: this.value || this.activeName,
        panes: []
      };
    },

    watch: {
      activeName(value) {
        this.setCurrentName(value);
      },
      value(value) {
        this.setCurrentName(value);
      }
    },

    computed: {
      currentTab() {
        let result;
        this.panes.forEach(tab => {
          if (this.currentName === (tab.name || tab.index)) {
            result = tab;
          }
        });
        return result;
      }
    },

    methods: {
      handleTabRemove(pane, event) {
        event.stopPropagation();
        const panes = this.panes;
        const currentTab = this.currentTab;

        let index = panes.indexOf(pane);

        if (index === -1) return;

        panes.splice(index, 1);
        pane.$destroy();

        this.$emit('tab-remove', pane);

        this.$nextTick(_ => {
          if (pane.active) {
            const panes = this.panes;
            let nextChild = panes[index];
            let prevChild = panes[index - 1];
            let nextActiveTab = nextChild || prevChild || null;

            if (nextActiveTab) {
              this.setCurrentName(nextActiveTab.name || nextActiveTab.index);
            }
            return;
          } else {
            this.setCurrentName(currentTab.name || currentTab.index);
          }
        });
      },
      handleTabClick(tab, tabName, event) {
        if (tab.disabled) return;
        this.setCurrentName(tabName);
        this.$emit('tab-click', tab, event);
      },
      setCurrentName(value) {
        this.currentName = value;
        this.$emit('input', value);
      },
      addPanes(item) {
        this.panes.push(item);
      },
      removePanes(item) {
        const panes = this.panes;
        const index = panes.indexOf(item);
        if (index > -1) {
          panes.splice(index, 1);
        }
      }
    },
    render(h) {
      let {
        type,
        handleTabRemove,
        handleTabClick,
        currentName,
        panes
      } = this;

      const tabs = this._l(panes, (pane, index) => {
        let tabName = pane.name || pane.index || index;
        if (currentName === undefined && index === 0) {
          this.setCurrentName(tabName);
        }

        pane.index = index;

        const btnClose = pane.isClosable
          ? <span class="el-icon-close" on-click={(ev) => { handleTabRemove(pane, ev); }}></span>
          : null;

        const tabLabelContent = pane.$slots.label || pane.label;
        return (
          <div
            class={{
              'el-tabs__item': true,
              'is-active': pane.active,
              'is-disabled': pane.disabled,
              'is-closable': pane.isClosable
            }}
            ref="tabs"
            refInFor
            on-click={(ev) => { handleTabClick(pane, tabName, ev); }}
          >
            {tabLabelContent}
            {btnClose}
          </div>
        );
      });

      return (
        <div class={{
          'el-tabs': true,
          'el-tabs--card': type === 'card',
          'el-tabs--border-card': type === 'border-card'
        }}>
          <div class="el-tabs__header">
            {!type ? <tab-bar tabs={panes}></tab-bar> : null}
            {tabs}
          </div>
          <div class="el-tabs__content">
            {this.$slots.default}
          </div>
        </div>
      );
    }
  };
</script>

<style>
@charset "UTF-8";
.el-tabs__header:after,
.el-tabs__header:before {
    display: table;
    content: ""
}

.el-tabs__header:after {
    clear: both
}

.el-tabs__header {
    border-bottom: 1px solid #d1dbe5;
    padding: 0;
    position: relative;
    margin: 0 0 15px
}

.el-tabs__active-bar {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 3px;
    background-color: #20a0ff;
    z-index: 1;
    transition: transform .3s cubic-bezier(.645, .045, .355, 1);
    list-style: none
}

.el-tabs__item {
    padding: 0 16px;
    height: 42px;
    box-sizing: border-box;
    line-height: 42px;
    float: left;
    list-style: none;
    font-size: 14px;
    color: #8391a5;
    margin-bottom: -1px;
    position: relative
}

.el-tabs__item .el-icon-close {
    border-radius: 50%;
    text-align: center;
    transition: all .3s cubic-bezier(.645, .045, .355, 1);
    margin-left: 5px
}

.el-tabs__item .el-icon-close:before {
    -ms-transform: scale(.7, .7);
    transform: scale(.7, .7);
    display: inline-block
}

.el-tabs__item .el-icon-close:hover {
    background-color: #97a8be;
    color: #fff
}

.el-tabs__item:hover {
    color: #1f2d3d;
    cursor: pointer
}

.el-tabs__item.is-disabled {
    color: #bbb;
    cursor: default
}

.el-tabs__item.is-active {
    color: #20a0ff
}

.el-tabs__content {
    overflow: hidden;
    position: relative
}

.el-tabs--card>.el-tabs__header>.el-tabs__active-bar {
    display: none
}

.el-tabs--card>.el-tabs__header>.el-tabs__item .el-icon-close {
    position: relative;
    font-size: 12px;
    width: 0;
    height: 14px;
    vertical-align: middle;
    line-height: 15px;
    overflow: hidden;
    top: -1px;
    right: -2px;
    -ms-transform-origin: 100% 50%;
    transform-origin: 100% 50%
}

.el-tabs--card>.el-tabs__header>.el-tabs__item.is-active.is-closable .el-icon-close,
.el-tabs--card>.el-tabs__header>.el-tabs__item.is-closable:hover .el-icon-close {
    width: 14px
}

.el-tabs--card>.el-tabs__header>.el-tabs__item {
    border: 1px solid transparent;
    transition: all .3s cubic-bezier(.645, .045, .355, 1)
}

.el-tabs--card>.el-tabs__header>.el-tabs__item.is-closable:hover {
    padding-right: 9px;
    padding-left: 9px
}

.el-tabs--card>.el-tabs__header>.el-tabs__item.is-active {
    border: 1px solid #d1dbe5;
    border-bottom-color: #fff;
    border-radius: 4px 4px 0 0
}

.el-tabs--card>.el-tabs__header>.el-tabs__item.is-active.is-closable {
    padding-right: 16px;
    padding-left: 16px
}

.el-tabs--border-card {
    background: #fff;
    border: 1px solid #d1dbe5;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04)
}

.el-tabs--border-card>.el-tabs__content {
    padding: 15px
}

.el-tabs--border-card>.el-tabs__header {
    background-color: #eef1f6;
    margin: 0
}

.el-tabs--border-card>.el-tabs__header>.el-tabs__item {
    transition: all .3s cubic-bezier(.645, .045, .355, 1);
    border: 1px solid transparent;
    border-top: 0;
    margin-right: -1px;
    margin-left: -1px
}

.el-tabs--border-card>.el-tabs__header>.el-tabs__item.is-active {
    background-color: #fff;
    border-right-color: #d1dbe5;
    border-left-color: #d1dbe5
}

.el-tabs--border-card>.el-tabs__header>.el-tabs__item.is-active:first-child {
    border-left-color: #d1dbe5
}

.el-tabs--border-card>.el-tabs__header>.el-tabs__item.is-active:last-child {
    border-right-color: #d1dbe5
}

.slideInLeft-transition,
.slideInRight-transition {
    display: inline-block
}

.slideInRight-enter {
    animation: slideInRight-enter .3s
}

.slideInRight-leave {
    position: absolute;
    left: 0;
    right: 0;
    animation: slideInRight-leave .3s
}

.slideInLeft-enter {
    animation: slideInLeft-enter .3s
}

.slideInLeft-leave {
    position: absolute;
    left: 0;
    right: 0;
    animation: slideInLeft-leave .3s
}

@keyframes slideInRight-enter {
    0% {
        opacity: 0;
        transform-origin: 0 0;
        transform: translateX(100%)
    }
    to {
        opacity: 1;
        transform-origin: 0 0;
        transform: translateX(0)
    }
}

@keyframes slideInRight-leave {
    0% {
        transform-origin: 0 0;
        transform: translateX(0);
        opacity: 1
    }
    100% {
        transform-origin: 0 0;
        transform: translateX(100%);
        opacity: 0
    }
}

@keyframes slideInLeft-enter {
    0% {
        opacity: 0;
        transform-origin: 0 0;
        transform: translateX(-100%)
    }
    to {
        opacity: 1;
        transform-origin: 0 0;
        transform: translateX(0)
    }
}

@keyframes slideInLeft-leave {
    0% {
        transform-origin: 0 0;
        transform: translateX(0);
        opacity: 1
    }
    100% {
        transform-origin: 0 0;
        transform: translateX(-100%);
        opacity: 0
    }
}
</style>



